import React from 'react';
import { connect } from 'react-redux';
import { 
    BrowserRouter as Router, Switch, Route, Redirect 
  } from 'react-router-dom';
  import {
    Navbar,
    Nav,
    NavItem,
    NavLink
  } from 'reactstrap';
import './App.css';
import Home from './components/home';
import StudentTable from './components/studenttable';
import FormInput from './components/form';


class App extends React.PureComponent {
  render() {
  return (
	<Router>
		<div className="text-center">
      <Navbar color="text-light" className="justify-content-center" light expand="md">
			  <Nav className="font-weight-bold">
          <NavItem>
					  <NavLink href="/react">Home</NavLink>
          </NavItem>
				  <NavItem>
					  <NavLink href="/react/form">Form</NavLink>
				  </NavItem>
				  <NavItem>
					  <NavLink href="/react/studentlist">Student List</NavLink>
				  </NavItem>
			  </Nav>
      </Navbar>
			<Switch>
        <Route 
            path="/react/form" 
            render={() => {
            if (this.props.post.edit === false) {
              return <Redirect to="/react/studentlist" />;
            }
            if (this.props.post.addRedirect === true) {
              return <Redirect to="/react/studentlist" />;
            }
            return <FormInput />;
          }}
        />
        <Route 
          path="/react/studentlist"
          render={() => {
            if (this.props.post.edit === true) {
              return <Redirect to="/react/form" />;
            }
            return (<StudentTable />);
          }}
        />
        <Route path="/react">
					<Home />
        </Route>
			</Switch>
		</div>
	</Router>
  );
}
}

const mapStatetoProps = (state) => ({
    post: state
});

export default connect(mapStatetoProps)(App);
